
#skill{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    text-align: center;
}
.skills{
    font-size: 2rem;
    font-weight: 700;
}

#skillscorner {
    border-radius: 20px;
    background: white;
    padding: 40px; 
    width: 40%;
    height: 100%;  
    color: black;
    border: 2px solid black;
    display: inline-block; 
    margin: 40px;
 }
.tick{
    width: 20px;
    height: 20px;
}
.experience{
    font-size: small;
}
