#contact{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    text-align: center;
}
.contactPage{
    font-size: 2rem;
    font-weight: 700;
}
.contactDes{
    font-size: 1rem;
    font-weight: 500;
}

.submitBtn{
    background-color: white;
    border: none;
    padding: 0.75rem 3.5rem;
    border-radius: 0.5rem;
    margin: 2rem;
    font-weight: bold;
}

.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    color: white;
    background-color: rgb(40,40,40);
    border-radius: 0.5rem;
    border: none;
    padding: 1rem 1rem;
    margin: 0.5rem;
    border: 1px solid white;
}

.link{
    object-fit: cover;
    height: 3rem;
    width: 5rem;
    margin: 0 0.75rem;
    padding-left: 30px;
}