#intro{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.introContent{
    padding: 2rem;
    height: 100vh;
    width: 100vw;
    font-size: 3rem;
    font-weight: 700;
    flex-direction: column;
    display: flex;
    justify-content: center;
}
.introName{
    color: gold;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
    padding-bottom: 1rem;
}
.introDes{
    font-size: 2rem;
    font-weight: 700;
}
.introPara{
    font-weight: 100;
    padding-top: 1rem;
    font-size: medium;
}
.profile{
   top:0;
   right:0;
   position: absolute;
   z-index: -1;
   object-fit: cover;
   height: 100vh;
   width: 500px;
}

#github{
   display: flex;
   padding-top: 15px;
}

.linked{
    width: 5rem;
    height: 5rem;
}