.navbar{
    height: 6rem;
    width: 100%;
    margin: 0 auto;
    padding: 1rem 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top:0;
    z-index: 3;
    background-color: rgb(15, 15, 15);
}
.logo{
    object-fit: cover;
    height: 5rem;
    width: 6rem;
}
.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
    font-size: 21px;
    padding: 1rem 2rem;
}

.desktopMenuBtn{
    color: black;
    background-color: white;
    display: flex;
    border: none;
    align-items: center;
    justify-content: center;
    padding: 1rem 2rem;
    border-radius: 2rem;
    font-size: medium;
}
.desktopMenuListItem:hover{
    color: gold;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid gold;
}
.active{
    color: gold;
    border-bottom: 3px solid gold;
    padding-bottom: 0.5rem;
}
.gmail{
    object-fit: cover;
    height: 1.5rem;
    width: 2rem;
    padding-right: 9px;
}

.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 5rem;
    background-color: rgb(40,40,40);
    border-radius: 1rem;
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 3rem;
}
.listItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background-color: rgb(30,30,30);
    border-radius: 5rem;
}

@media screen and (max-width:720px){
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}