#certification {
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    text-align: center;
 }
 .course{
    background: white;
    border-radius: 15px;
    padding: 30px; 
    height: auto;  
    color: black;
    border: 2px solid black;
    margin: 40px;
    text-align: center;
    max-width: 60rem;
 }
.certificationTitle{
    font-size: 2rem;
    font-weight: 700;
}
.udemy{
    object-fit: cover;
    height: 3rem;
    width: 8rem;
    padding-right: 20px;
}
.nptel{
    object-fit: cover;
    height: 2rem;
    width: 10rem;
    padding-right: 20px;
}